@import '../../style/colors.scss';

.stage {
  display: block;
  width: 100%;
  height: 52%;
  min-height: 430px;
  overflow: hidden;

  background-color: $primary;

  transform: translateY(-100%);
  transition: transform 1s ease-out;

  &--mounted {
    transform: translateY(0%);
  }
  

  &__title { 
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    letter-spacing: 5px;

    @include small {

    }

    h1 {
      font-size: 32px;
      margin: 10px;

      @include small {
        font-size: 50px;
        margin: 20px;
      }
  
    }
    h3 {
      //font-style: italic;
      margin: 5px;
      font-size: 16px;

      @include small {
        font-size: 22px;
        margin: 10px;
      }
    }
  }
  &__canvas{
    display: block;
    position: absolute;
    margin: 0px;
    padding: 0px;
  }
}


